import React from 'react';
import { Button } from 'antd';
import { SyncOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';

const selector = (store: any) => ({
  setNeedRefreshAt: () => store.setStore({ key: 'needRefreshAt', value: Date.now() }),
});

function RefreshFilesBtn() {
  const { setNeedRefreshAt } = useStore(selector, shallow);

  return (
    <Button icon={<SyncOutlined />} onClick={setNeedRefreshAt}>刷新</Button>
  );
}

export default RefreshFilesBtn;
