/**
 * 生成guid
 */
export const guid = () => {
  return (
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
  );
};

/**
 * 获取一个永不重复的 UUID
 * @returns uuid {string}
 */
export const getUuid = () => {
  return Number(Math.random().toString().substr(2, 5) + Date.now()).toString(36);
};

/**
 * 获取文件名
 */
export const getFileName = (filename: string) => {
  const idx = filename.indexOf('.');
  return filename.substr(0, idx).trim().replace(/\s+/g, '-');
};

/**
 * 获取文件后缀
 */
export const getFileSuffix = (filename: string) => {
  const idx = filename.lastIndexOf('.');
  return idx > 0 ? (filename.substring(idx + 1).toLowerCase()) : null;
};

type TWeight = 'B'|'KB'|'MB'|'GB'|'TB'|'PB'|'EB'|'ZB'|'YB';
/**
 * 文件大小格式化
 */
export const getFileSize = (value?: string, weight?: TWeight) => {
  if (!value) { return '0 B'; }
  const _unitArr = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
  const unitArrIdx = _unitArr.indexOf(weight || 'YB');
  const unitArr = _unitArr.slice(0, unitArrIdx + 1);
  let index = 0;
  const valuesize = parseFloat(value);
  index = Math.floor(Math.log(valuesize) / Math.log(1024));
  const size = +(valuesize / (1024 ** index)).toFixed(2);
  return `${size} ${unitArr[index]}`;
};

/**
 * 判断文件类型是否为图片格式
 */
export const isImage = (suffix: string): boolean => {
  return fileTypeList[suffix] === 'image';
};

/**
 * 判断是否能压缩的图片格式
 */
export const isNeedCompress = (suffix: string): boolean => {
  return /(png|jpg|jpeg|webp|avif)$/.test(suffix);
};

/**
 * 判断是否能加水印的图片格式
 */
export const isNeedWatermark = (suffix: string): boolean => {
  return /(png|jpg|jpeg|webp|avif)$/.test(suffix);
};

/**
 * 复制文本到系统剪贴板
 */
export const copyText = async (txt: string) => {
  return await navigator.clipboard.writeText(txt);
};

/**
 * 下载文件
 */
export const downloadByFile = (file: File) => {
  const url = URL.createObjectURL(file);// 创建图片 URL
  const link = document.createElement('a'); // 创建一个 a 标签
  link.href = url; // 设置链接地址为图片 URL
  link.download = file.name; // 设置下载文件的文件名
  document.body.appendChild(link); // 将 a 标签添加到 body 中
  link.click(); // 模拟点击链接进行下载
  document.body.removeChild(link); // 下载完成后移除 a 标签
  URL.revokeObjectURL(url); // 释放图片 URL
};

/**
 * 下载文件
 */
export const downloadByUrl = (url: string) => {
  const link = document.createElement('a'); // 创建一个 a 标签
  link.href = url; // 设置链接地址为图片 URL
  document.body.appendChild(link); // 将 a 标签添加到 body 中
  link.click(); // 模拟点击链接进行下载
  document.body.removeChild(link); // 下载完成后移除 a 标签
};


/**
 * file 转 base64
 */
export const fileToBase64 = (file: File) => {
  return new Promise((resolve) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      const base64 = reader.result as string;
      resolve(base64);
    };
    reader.onerror = () => resolve(null);
  });
};

/**
 * 处理获取的文件
 */
export const gettingFilesHandle = (file: File) => {
  return new Promise(async (resolve) => {
    if (!file) { resolve(null); }
    // 转换 base64
    const base64: any = (await fileToBase64(file)) || '';
    // 判断是否超出大小 B
    if (base64.length >= FILE_UPLOAD_MAX_SIZE * 1024 * 1024) {
      tips('error', `${file.name} 超过 ${FILE_UPLOAD_MAX_SIZE} MB，跳过选择`);
      resolve(null);
    }

    resolve({
      uuid: guid(),
      base64,
      file,
      info: {
        name: getFileName(file.name),
        suffix: getFileSuffix(file.name),
        nameType: 'none',
        extend: null,
        hash: getUuid(),
        time: Date.now(),
      },
    });
  });
};
