import React from 'react';
import {
  FolderFilled,
  FileUnknownFilled,
  FileMarkdownFilled,
  FileImageFilled,
  FileWordFilled,
  FileTextFilled,
  FilePdfFilled,
  FilePptFilled,
  FileZipFilled,
  FileExcelFilled,
  VideoCameraFilled,
  CustomerServiceFilled,
  CodeSandboxOutlined,
  // FolderTwoTone,
  // FileWordTwoTone,
  // FileTextTwoTone,
  // FileUnknownTwoTone,
  // FilePdfTwoTone,
  // FilePptTwoTone,
  // FileZipTwoTone,
  // FileExcelTwoTone,
  // FileImageTwoTone,
  // FileMarkdownTwoTone,
  // VideoCameraTwoTone,
  // CustomerServiceTwoTone,
} from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import classnames from 'classnames';
import { staticalyPath, getFileSuffix } from '../../utils/tools';
import { fileTypeList } from '../../utils/constMap';
import styles from './index.less';

interface IProps {
  data: any;
  needImagePreview: boolean;
  size?: number;
}

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
});

function FileCover({ data, needImagePreview, size }: IProps) {
  const { curLibrary } = useStore(selector, shallow);

  const renderTypeCover = (type: string) => {
    const iconType: any = {
      'dir': <FolderFilled style={{ fontSize: size, color: '#ffca5b' }} />,
      'image': <FileImageFilled style={{ fontSize: size, color: '#7cb305' }} />,
      'video': <VideoCameraFilled style={{ fontSize: size, color: '#597ef7' }} />,
      'audio': <CustomerServiceFilled style={{ fontSize: size, color: '#ff4d4f' }} />,
      'model': <CodeSandboxOutlined style={{ fontSize: size, color: '#383948' }} />,
      'pdf': <FilePdfFilled style={{ fontSize: size, color: '#fe3e3c' }} />,
      'excel': <FileExcelFilled style={{ fontSize: size, color: '#0c9d4d' }} />,
      'doc': <FileWordFilled style={{ fontSize: size, color: '#2175ff' }} />,
      'txt': <FileTextFilled style={{ fontSize: size, color: '#69b1ff' }} />,
      'zip': <FileZipFilled style={{ fontSize: size, color: '#ad4e00' }} />,
      'ppt': <FilePptFilled style={{ fontSize: size, color: '#ff5f39' }} />,
      'markdown': <FileMarkdownFilled style={{ fontSize: size, color: '#08979c' }} />,
      'unknown': <FileUnknownFilled style={{ fontSize: size, color: '#595959' }} />,

      // 'dir': <FolderTwoTone twoToneColor="#ffca5b" style={{ fontSize: size }} />,
      // 'image': <FileImageTwoTone twoToneColor="#7cb305" style={{ fontSize: size }} />,
      // 'video': <VideoCameraTwoTone twoToneColor="#597ef7" style={{ fontSize: size }} />,
      // 'audio': <CustomerServiceTwoTone twoToneColor="#ff4d4f" style={{ fontSize: size }} />,
      // 'pdf': <FilePdfTwoTone twoToneColor="#fe3e3c" style={{ fontSize: size }} />,
      // 'excel': <FileExcelTwoTone twoToneColor="#0c9d4d" style={{ fontSize: size }} />,
      // 'doc': <FileWordTwoTone twoToneColor="#2175ff" style={{ fontSize: size }} />,
      // 'txt': <FileTextTwoTone twoToneColor="#69b1ff" style={{ fontSize: size }} />,
      // 'zip': <FileZipTwoTone twoToneColor="#ad4e00" style={{ fontSize: size }} />,
      // 'ppt': <FilePptTwoTone twoToneColor="#ff5f39" style={{ fontSize: size }} />,
      // 'markdown': <FileMarkdownTwoTone twoToneColor="#08979c" style={{ fontSize: size }} />,
      // 'unknown': <FileUnknownTwoTone twoToneColor="#595959" style={{ fontSize: size }} />,
    };
    return (
      <div className={styles.thumbImage}>
        {iconType[type] || iconType.unknown}
      </div>
    );
  };

  // 文件后缀
  const fileSuffix = data.type === 'file' ? getFileSuffix(data.name) : null;
  // 文件类型
  const fileType = data.type === 'dir' ? 'dir' : fileTypeList[fileSuffix || ''] || 'unknown';
  let fileCover = renderTypeCover(fileType);
  if (fileType === 'image' && needImagePreview) {
    if (data.path) {
      fileCover = <img src={staticalyPath(curLibrary, data.path)} />;
    } else if (data.base64) {
      fileCover = <img src={data.base64} />;
    }
  }

  return (
    <div className={styles.view}>
      {fileCover}
    </div>
  );
}

export default FileCover;
