import request from "../utils/request";
import { github } from '../utils/constMap';
import { getGithubOwner, getGithubRepo, getBranch } from '../utils/tools';

/**
 * 素材库 是基于指定的一个github仓库作为素材库基池
 */

// 获取分支列表
export const getBranchList = () => {
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  const branch = getBranch();
  return request.get({
    url: `${github.host}/repos/${owner}/${repo}/contents`,
    data: {
      ref: branch,
    }
  });
};

// 获取指定路径 Path 下的目录和文件
export const getContent = ({ library, path }: { library: string, path?: string }) => {
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  const branch = getBranch();
  return request.get({
    url: `${github.host}/repos/${owner}/${repo}/contents/${library}` + `${path ? ('/' + path) : ''}`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      ref: branch,
    }
  });
};

// 上传指定路径 Path 下的文件
export const uploadContent = (
  options: {
    library: string,
    path: string,
    content: string,
    message: string,
  }
) => {
  const {
    library,
    path,
    content,
    message,
  } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  const branch = getBranch();
  return request.put({
    url: `${github.host}/repos/${owner}/${repo}/contents/${library}/${path}`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      ref: branch,
      content,
      message,
    }
  });
};

// 删除指定路径 Path 下的目录和文件
export const deleteContent = (
  options: {
    library: string,
    path: string,
    sha: string,
    message: string,
  }
) => {
  const {
    library,
    path,
    sha,
    message,
  } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  const branch = getBranch();
  return request.delete({
    url: `${github.host}/repos/${owner}/${repo}/contents/${library}/${path}`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      ref: branch,
      sha,
      message,
    }
  });
};

// 上传图片获取 blob
export const uploadFileBlob = (options: {
  file: any,
}) => {
  const { file } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.post({
    url: `${github.host}/repos/${owner}/${repo}/git/blobs`,
    data: {
      owner,
      repo,
      content: (
        file.base64.compressBase64 ||
        file.base64.watermarkBase64 ||
        file.base64.originalBase64
      ).split(',')[1],
      encoding: 'base64'
    }
  });
};

// 创建 blobs 的 tree
export const createTree = (
  options: {
    sha: string,
    blobs: any[],
  }
) => {
  const {
    sha,
    blobs,
  } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.post({
    url: `${github.host}/repos/${owner}/${repo}/git/trees`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      tree: blobs.map((blob: any) => ({
        path: blob.path,
        mode: '100644',
        type: 'blob',
        sha: blob.sha
      })),
      base_tree: sha || null
    }
  });
};

// 创建 commit
export const createCommit = (
  options: {
    sha: string,
    tree: any
  }
) => {
  const {
    sha,
    tree,
  } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.post({
    url: `${github.host}/repos/${owner}/${repo}/git/commits`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      tree: tree.sha,
      parents: [sha],
      message: 'Upload files'
    }
  });
};

// 创建 ref
export const createRef = (
  options: {
    branch: string,
    sha: string,
  }
) => {
  const {
    branch,
    sha,
  } = options;
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request._core(
    `${github.host}/repos/${owner}/${repo}/git/refs/heads/${branch}`,
    {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      sha: sha,
    },
    'PATCH'
  );
};
