import React, { useState } from 'react';
import { Button, Upload } from 'antd';
import { CloudUploadOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import Modal from './modal';
import { gettingFilesHandle } from '@/module/utils/tools';

const selector = (store: any) => ({
  beforeUploadList: store.beforeUploadList,
  setBeforeUploadList: (list: any[]) => store.setStore({ key: 'beforeUploadList', value: list }),
});

interface IProps {
  type?: 'text';
  className?: string;
}

function UploadFileBtn({ type, className }: IProps) {
  const { beforeUploadList, setBeforeUploadList } = useStore(selector, shallow);

  const [open, setOpen] = useState<boolean>(false);

  const showModal = () => setOpen(true);

  const handleCancel = () => {
    setOpen(false);
    setBeforeUploadList([]);
  };

  const handleOk = () => {
    setOpen(false);
    // TODO
    setBeforeUploadList([]);
  };

  const handleBeforeUpload = async (file: any) => {
    const res = await gettingFilesHandle(file);
    if (res) {
      beforeUploadList.push(res);
      setBeforeUploadList(beforeUploadList);
    }
    return file;
  };

  const handleUploadChange = () => {
    if (!open) {
      showModal();
    }
  };

  return (
    <>
      <Upload
        name="file"
        multiple
        showUploadList={false}
        beforeUpload={handleBeforeUpload}
        onChange={handleUploadChange}
        customRequest={() => false}
      >
        {type === 'text' ? <span className={className}>文件上传</span> : (
          <Button
            type="primary"
            icon={<CloudUploadOutlined />}
          >
            文件上传
          </Button>
        )}
      </Upload>
      <Modal handleCancel={handleCancel} handleOk={handleOk} open={open} />
    </>
  );
}

export default UploadFileBtn;
