/**
 * 素材库
 */
import { create } from 'zustand';

export const useStore = create((set: any, get: any) => ({
  // 当前库
  curLibrary: '',
  // 当前路径
  curPaths: [],
  // 库列表
  librarys: [],
  // 素材列表展示样式 卡片｜列表
  fileListType: 'card',
  // 素材列表
  fileList: [],
  // 刷新素材列表
  needRefreshAt: null,
  // 查询素材列表loading
  searchFileListLoading: false,
  // 上传前列表
  beforeUploadList: [],
  // 消息中心 - 上传列表
  uploadList: [],
  // 更换数据
  setStore({ key, value }: { key: string, value: any }) {
    const store = { [key]: value };
    if (key === 'curLibrary') {
      store.curPaths = [];
    }
    set(store);
  },
}));
