import React from 'react';
import { UsbOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import CreateDirBtn from '../CreateDirBtn';
import UploadFileBtn from '../UploadFileBtn';
import { NEW_DIR_COUNT_MAX } from '@/module/utils/constMap';
import styles from './index.less';

const selector = (store: any) => ({
  curPaths: store.curPaths,
});

function Empty() {
  const { curPaths } = useStore(selector, shallow);

  return (
    <div className={styles.container}>
      <div className={styles.panel}>
        <div className={styles.icon}><UsbOutlined /></div>
        <div className={styles.title}>文件夹为空</div>
        <div className={styles.desc}>
          <span>点击浏览</span>
          <UploadFileBtn type="text" className={styles.upload} />
          {curPaths.lenth < NEW_DIR_COUNT_MAX && (
            <>
              <span>或</span>
              <CreateDirBtn type="text" className={styles.upload} />
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default Empty;
