import React, { useState } from 'react';
import { Button, Input } from 'antd';
import { FolderAddOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import * as controller from '@/module/controller';
import tips from '../Tips';
import Modal from '../Modal';
import styles from './index.less';

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
  curPaths: store.curPaths,
  setNeedRefreshAt: () => store.setStore({ key: 'needRefreshAt', value: Date.now() }),
});

interface IProps {
  type?: 'text';
  className?: string;
}

function CreateDirBtn({ type, className }: IProps) {
  const { curLibrary, curPaths, setNeedRefreshAt } = useStore(selector, shallow);

  const [open, setOpen] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [value, setValue] = useState<string>('');

  const showModal = () => setOpen(true);
  const onInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    if (!loading) {
      setValue(e.target.value);
    }
  };
  const handleCancel = () => setOpen(false);
  const handleOk = async () => {
    if (loading) { return null; }
    setLoading(true);
    controller.createForder(
      value,
      {
        library: curLibrary,
        path: curPaths.join('/') || null,
      }).then((result: any) => {
        if (result.success) {
          tips('success', '新文件夹创建成功');
          setLoading(false);
          setOpen(false);
          setValue('');
          setNeedRefreshAt();
        } else {
          tips('error', '新文件夹创建失败');
        }
      });
  };

  return (
    <>
      {type === 'text' ? <span onClick={showModal} className={className}>新文件夹</span> : (
        <Button type="primary" ghost icon={<FolderAddOutlined />} onClick={showModal}>新文件夹</Button>
      )}

      <Modal
        open={open}
        title="新建文件夹"
        okText="创建"
        cancelText="取消"
        onOk={handleOk}
        onCancel={handleCancel}
        confirmLoading={loading}
      >
        <div className={styles.view}>
          <Input placeholder="请输入文件夹名" onChange={onInputChange} value={value} />
        </div>
      </Modal>
    </>
  );
}

export default CreateDirBtn;
