import * as server from '../services/api';
import storage from '@/utils/storage';

const storageKey = {
  librarys: '__mytab_pics_librarys__',
  list: '__mytab_pics_files__',
};

const getResult = (success: boolean, data: any = null) => {
  return success ? { success: true, data } : { success: false, msg: data || '操作异常，请刷新看看' };
};

// 获取素材库
export const getLibrarys = async (isRefresh = false) => {
  let result = [];
  let needRequest = true;
  if (!isRefresh) {
    const storageResult = storage.get(storageKey.librarys, { list: [] });
    result = storageResult.list;
    needRequest = result.length === 0 || (Date.now() - storageResult.at >= 86400000);
  }
  if (needRequest) {
    result = await server.getBranchList() || [];
    if (!result?.message) {
      result = result.filter((i: any) => i.type === 'dir');
      storage.set(storageKey.librarys, {
        at: Date.now(),
        list: result,
      });
    }
  }
  return result;
};

// 获取指定库的文件列表数据
export const getFileList = async ({ library, path }: { library: string, path?: string }) => {
  let result = {};
  const resultGetContent = await server.getContent({ library, path }) || [];
  if (resultGetContent.msg) {
    result = getResult(false);
  } else {
    result = getResult(true, resultGetContent.filter((i: any) => i.name !== '.init'));
  }
  // 过滤掉初始化兜底的.init文件
  return result;
};

// 创建文件夹
export const createForder = async (
  forderName: string,
  { library, path }: { library: string, path?: string }
) => {
  const _path = `${path ? (path + '/') : ''}${forderName}/.init`;
  const resultCreateInitFile = await server.uploadContent({
    library,
    path: _path,
    message: 'add a forder',
    content: '5q2k5paH5Lu255So5LqO5Yib5bu65paH5Lu25aS5',
  });
  let result = {};
  if (resultCreateInitFile?.content?.sha) {
    result = getResult(true, resultCreateInitFile);
    // await server.deleteContent({
    //   branch,
    //   path: _path,
    //   message: 'remove init file',
    //   sha: resultCreateInitFile?.content?.sha,
    // });
  } else {
    result = getResult(false);
  }
  return result;
};

// 删除单个文件
export const deleteSingleFile = async (
  { library, path, sha }: { library: string, path?: string, sha: string, }
) => {
  const _path = `${path ? (path + '/') : ''}`;
  const resultDeleteFile = await server.deleteContent({
    library,
    path: _path,
    message: 'remove file',
    sha,
  });
  return resultDeleteFile;
};

