import React, { useState } from 'react';
import { Button, Input, Radio, Checkbox, Upload } from 'antd';
import type { RadioChangeEvent } from 'antd';
import type { CheckboxValueType } from 'antd/es/checkbox/Group';
// import { CloudUploadOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import Modal from '../Modal';
import FileCover from '../FileCover';
import { isImage } from '@/module/utils/tools';
import styles from './index.less';

interface IProps {
  open: boolean;
  handleCancel: () => void;
  handleOk: () => void;
}

const selector = (store: any) => ({
  beforeUploadList: store.beforeUploadList,
  setBeforeUploadList: (list: any[]) => store.setStore({ key: 'beforeUploadList', value: list }),
});

function UploadFileBtn({
  open,
  handleCancel,
  handleOk,
}: IProps) {
  const { beforeUploadList, setBeforeUploadList } = useStore(selector, shallow);

  const [loading, setLoading] = useState<boolean>(false);

  const onInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, uuid: string) => {
    if (loading) { return null; }
    const idx = beforeUploadList.findIndex((v: any) => v.uuid === uuid);
    if (idx !== -1) {
      beforeUploadList[idx].info.name = e.target.value;
      setBeforeUploadList(beforeUploadList);
    }
  };

  const onRadioChange = (e: RadioChangeEvent, uuid: string) => {
    if (loading) { return null; }
    const idx = beforeUploadList.findIndex((v: any) => v.uuid === uuid);
    if (idx !== -1) {
      beforeUploadList[idx].info.nameType = e.target.value;
      if (e.target.value === 'none') {
        beforeUploadList[idx].info.extend = null;
      }
      if (e.target.value === 'hash') {
        beforeUploadList[idx].info.extend = beforeUploadList[idx].info.hash;
      }
      if (e.target.value === 'time') {
        beforeUploadList[idx].info.extend = beforeUploadList[idx].info.time;
      }
      setBeforeUploadList(beforeUploadList);
    }
  };

  const onCheckboxChange = (checkedValues: CheckboxValueType[], uuid: string) => {
    if (loading) { return null; }
    const idx = beforeUploadList.findIndex((v: any) => v.uuid === uuid);
    if (idx !== -1) {
      beforeUploadList[idx].info.setting = checkedValues;
      setBeforeUploadList(beforeUploadList);
    }
  };

  const removeItem = (uuid: string) => {
    const idx = beforeUploadList.findIndex((v: any) => v.uuid === uuid);
    if (idx !== -1) {
      beforeUploadList[idx].isRemove = true;
      setBeforeUploadList(beforeUploadList);
    }
  };

  return (
    <Modal
      open={open}
      title="文件上传"
      okText="上传全部"
      cancelText="取消"
      onOk={handleOk}
      onCancel={handleCancel}
      confirmLoading={loading}
      width={700}
    >
      <div className={styles.list}>
        {beforeUploadList.map((i: any) => (
          <div className={styles.item} key={i.uuid}>
            <div className={styles.cover}>
              <FileCover
                data={{ type: 'file', name: i.file.name, base64: i.base64 }}
                needImagePreview
                size={48}
              />
            </div>
            <div className={styles.info}>
              <div className={styles.title}>
                <div className={styles.label}>文件名称</div>
                <div className={styles.value}>
                  <Input
                    placeholder="请输入文件名"
                    onChange={e => onInputChange(e, i.uuid)}
                    value={i.info.name}
                    addonAfter={`.${[i.info.extend, i.info.suffix].filter(i => i).join('.')}`}
                  />
                </div>
              </div>
              <div className={styles.actions}>
                <div className={styles.action}>
                  <div className={styles.label}>名称设置</div>
                  <div className={styles.value}>
                    <Radio.Group
                      onChange={e => onRadioChange(e, i.uuid)}
                      value={i.info.nameType || 'none'}
                    >
                      <Radio value="none">无</Radio>
                      <Radio value="hash">哈希化</Radio>
                      <Radio value="time">时间戳</Radio>
                    </Radio.Group>
                  </div>
                </div>
                {isImage(i.info.suffix) && (
                  <div className={styles.action}>
                    <div className={styles.label}>图片设置</div>
                    <div className={styles.value}>
                      <Checkbox.Group
                        options={[
                          { label: '水印', value: 'watermark' },
                          { label: '压缩', value: 'compress' },
                        ]}
                        value={i.info.setting}
                        onChange={checkedValues => onCheckboxChange(checkedValues, i.uuid)}
                      />
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>
    </Modal>
  );
}

export default UploadFileBtn;
