import React from 'react';
import {
  Button,
  Tag,
  Dropdown,
  Checkbox,
  Modal,
  Divider,
  Space
} from 'antd';
import {
  PlaySquareFilled,
  PictureFilled,
  AudioFilled,
  FolderOpenFilled,
  FileFilled,
  MoreOutlined,
} from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import classnames from 'classnames';
import FileCover from '../FileCover';
import tips from '../Tips';
import {
  getFileSuffix, getFileSize,
  staticalyPath,
  chinaJsDelivrPath, jsDelivrPath, gitHubPath,
  copyText, downloadByUrl
} from '../../utils/tools';
import { fileTypeList } from '../../utils/constMap';
import styles from './index.less';

interface IProps {
  data: any;
  onClick: () => void;
}

const selector = (store: any) => ({
  fileListType: store.fileListType,
  curLibrary: store.curLibrary,
});

function Item({ data, onClick }: IProps) {
  const { curLibrary, fileListType } = useStore(selector, shallow);

  // 分享
  const handleShare = async (type: string) => {
    let url = '';
    if (type === 'gitHubPath') { url = gitHubPath(curLibrary, data.path); }
    if (type === 'staticalyPath') { url = staticalyPath(curLibrary, data.path); }
    if (type === 'chinaJsDelivrPath') { url = chinaJsDelivrPath(curLibrary, data.path); }
    if (type === 'jsDelivrPath') { url = jsDelivrPath(curLibrary, data.path); }
    await copyText(url);
    tips('success', '复制成功');
  };

  // 删除
  const handleRemove = async () => {
    tips('warning', '暂不支持');
  };

  // 文件后缀
  const fileSuffix = data.type === 'file' ? getFileSuffix(data.name) : null;
  // 文件大小
  const fileSize = getFileSize(data.size);
  // 文件类型
  const fileType = data.type === 'dir' ? 'dir' : fileTypeList[fileSuffix || ''] || 'unknown';
  let fileTypeIcon = null;
  if (fileType === 'dir') {
    fileTypeIcon = <FolderOpenFilled style={{ color: "#fa8c16" }} />;
  } else if (fileType === 'image') {
    fileTypeIcon = <PictureFilled style={{ color: "#2f54eb" }} />;
  } else if (fileType === 'video') {
    fileTypeIcon = <PlaySquareFilled style={{ color: "#2f54eb" }} />;
  } else if (fileType === 'audio') {
    fileTypeIcon = <AudioFilled style={{ color: "#2f54eb" }} />;
  } else {
    fileTypeIcon = <FileFilled style={{ color: "#bfbfbf" }} />;
  }

  const shareMenuProps: any = {
    items: [{
      key: 'gitHubPath',
      label: 'GitHub',
    },
    {
      key: 'staticalyPath',
      label: 'Staticaly',
    },
    {
      key: 'chinaJsDelivrPath',
      label: 'ChinaJsDelivr',
    },
    {
      key: 'jsDelivrPath',
      label: 'JsDelivr',
    }].filter(i => fileType === 'dir' ? i.key === 'gitHubPath' : true),
    onClick: async (e: any) => {
      // 分享
      if ([
        'staticalyPath', 'gitHubPath', 'chinaJsDelivrPath', 'jsDelivrPath'
      ].includes(e.key)) {
        await handleShare(e.key);
      }
    },
  };

  const dropdownMenuProps: any = {
    items: [{
      key: 'share',
      label: '分享',
      children: shareMenuProps.items,
    }, {
      type: 'divider',
    }, {
      key: 'remove',
      label: '删除',
    }],
    onClick: async (e: any) => {
      if (e.key === 'remove') {
        handleRemove();
      } else {
        // 分享
        shareMenuProps.onClick(e);
      }
    },
  };

  return (
    <div key={`${fileListType}-${data.uuid}`} className={classnames(styles.item, fileListType === 'card' && styles.card, fileListType === 'list' && styles.list)}>
      <div className={classnames(styles.wrap, data.type === 'dir' && styles.dir)}>
        <div className={styles.panel}>
          <div className={styles.cover} onClick={onClick}>
            <div className={styles.view}>
              <FileCover data={data} needImagePreview={fileListType === 'card'} size={fileListType === 'card' ? 48 : 22} />
            </div>
          </div>
          <div className={styles.info} onClick={onClick}>
            <div className={styles.title}>{data.name}</div>
            <div className={styles.attr}>
              <div className={styles.tags}>
                <Space>
                  {fileSuffix && <Tag color="geekblue">{fileSuffix.toUpperCase()}</Tag>}
                  {data.type !== 'dir' && <Tag color="purple">{fileSize}</Tag>}
                </Space>
              </div>
              <div className={styles.fileTypeIcon}>{fileTypeIcon}</div>
            </div>
          </div>
          <div className={styles.more}>
            {fileListType === 'card' ? (
              <Dropdown menu={dropdownMenuProps} placement="topRight" getPopupContainer={triggerNode => triggerNode!.parentElement!.parentElement!}>
                <Button size="small" icon={<MoreOutlined rotate={90} />} />
              </Dropdown>
            ) : (
              <Space>
                <Dropdown menu={shareMenuProps} placement="topRight" getPopupContainer={triggerNode => triggerNode!.parentElement!.parentElement!}>
                  <Button size="small">分享</Button>
                </Dropdown>
                <Button size="small" onClick={handleRemove}>删除</Button>
              </Space>
            )}
          </div>
          <div className={styles.mult}>
            {fileType !== 'dir' && (
              <Checkbox />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Item;
