import React from 'react';
import {
  Button,
  Segmented,
  Space,
  Divider,
} from 'antd';
import {
  AppstoreOutlined,
  BarsOutlined,
  CloudDownloadOutlined,
  CloudUploadOutlined,
  DeleteOutlined,
  SyncOutlined,
} from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import CreateDirBtn from '../CreateDirBtn';
import UploadFileBtn from '../UploadFileBtn';
import TaskCenterBtn from '../TaskCenterBtn';
import RefreshFilesBtn from '../RefreshFilesBtn';
import { NEW_DIR_COUNT_MAX } from '@/module/utils/constMap';
import styles from './index.less';

const selector = (store: any) => ({
  curPaths: store.curPaths,
  fileListType: store.fileListType,
  setFileListType: (value: string) => store.setStore({ key: 'fileListType', value }),
});

interface IProps {}

function ToolBar({}: IProps) {
  const { fileListType, setFileListType, curPaths } = useStore(selector, shallow);
  const isMult = false;
  return (
    <Space size={8}>
      {isMult ? (
        <>
          {/* <Button type="primary" icon={<CloudDownloadOutlined />}>批量下载</Button> */}
          <Button type="primary" icon={<CloudDownloadOutlined />}>批量分享</Button>
          <Button type="primary" ghost icon={<DeleteOutlined />}>批量删除</Button>
        </>
      ) : (
        <>
          <UploadFileBtn />
          {curPaths.length < NEW_DIR_COUNT_MAX && (<CreateDirBtn />)}
          <RefreshFilesBtn />
        </>
      )}
      <Divider type="vertical" />
      <Segmented
        value={fileListType}
        onChange={(value) => setFileListType(`${value}`)}
        options={[
          { value: 'card', icon: <AppstoreOutlined /> },
          { value: 'list', icon: <BarsOutlined /> }
        ]}
      />
      <TaskCenterBtn />
    </Space>
  );
}

export default ToolBar;
